import React, { Fragment } from 'react';
import * as C from "../component/Components"
import * as I from '../img/images'
import * as S from '../Styles'

export default class ContactPage extends React.Component{
  constructor(props){
    super(props);
    this.contactFormWrap = {
      paddingTop: 50,
      backgroundColor: "#005" 
    }
  }
  render(){
    return(
      <C.Row center style={this.contactFormWrap}>
        <iframe src="https://docs.google.com/forms/d/e/1FAIpQLScRjSV42bxAozjE9GHsdrv6GUZFoNFU9qlHpKNNaZLgiw_uIQ/viewform?embedded=true" width="100%" height="100%" />
      </C.Row>
    );
  }
}
