import React from 'react';
import * as C from "../component/Components"
import * as P from "./Pages";

export default class Main extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      currentPage : 0,
    }
  }
  render(){
    const { currentPage } = this.state;
    let Contents = null;
    switch(currentPage){
      case 0 : {
        Contents = 
          <P.TopPage 
            toContactPage = {()=>this.setState({currentPage : 1})}
          />
        break;
      }
      case 1 : {
        Contents = 
          <P.ContactPage/>
        break;
      }
      default : { 
        Contents = null;
        break; 
      }
    }

    return(
      <C.Grid>
        <P.Header />
        {Contents}
      </C.Grid>
    );
  }
} 