import React, { Fragment } from 'react';
import * as C from "../component/Components"
import * as I from '../img/images'
import * as S from '../Styles'


export default class TopPage extends React.Component{
  render(){
    const { toContactPage } = this.props
    const topBg = {
      paddingTop: 50,
      background:`url(${I.businessman})  no-repeat`,
      backgroundPosition:"center top",
      backgroundSize:"auto 100vh",
      minHeight: '100vh',  
    }
    const top = {
      height: '50vh',
    }
    // const topImage1 = {
    //   background:`url(${I.success})  no-repeat`,
    //   backgroundPosition:"center top",
    //   // backgroundSize:"100%",
    //   minHeight: '100vh',
    // }
    // const topImage2 = {
    //   background:`url(${I.syukatsu})  no-repeat`,
    //   backgroundPosition:"center top",
    //   backgroundSize:"100% 100%",
    //   width: 400,
    //   height: 400,
    //   minWidth: 400
    // }
    const catchcopy = {
      fontSize: 150,
      color: '#fff',
      fontWeight: 'bolder',
      // textShadow: "3px 3px 3px 3px #FF0"
    } 
    const subCatchcopy = {
      marginTop: 32,
      fontSize: 50,
      color: '#fff'
    }
    const contactBtn ={
      marginTop: 32,
      fontSize: 25,
      height: 100,
      width: 500,
    }
    const contactBtn_S = {
      height: 50,
      width: 200,
      fontSize: 16,
    }
    const contactBtn_M = {
      height: 70,
      width: 300,
      fontSize: 20,
    }
    const section = {
      // minHeight:'100vh',
    }
    const sectionTitle = {
      padding:'32px 0px',
      height: null,
      marginTop: 42, 
      fontSize: 32,
      fontWeight: 'bolder',
    }
    const aboutUsIcon = {
      height: 'auto',
      maxWidth: '100%',
      padding : 32, 
    }
    const aboutUsSentence ={
      fontSize: 20,
      lineHeight: 4,
    }
    const aboutActHeading = {
      fontSize: 20,
      // marginBottom: 24,
      fontWeight: 'bold',
    }
    const aboutActSentence = {
      maxWidth: 600,
      lineHeight: 1.2,
      padding:24,
    }
    const aboutActImg = {
      cursor: null,
      height: '100%',
      maxHeight: '25vh',
      borderRadius: 30,
      minWidth: 300,
      maxWidth: "95vw"

    }
    const recommendImg = {
      height: 250,
      width: 250,
    }
    const recommendWords = {
      marginTop: 24,
      fontSize: 20,
    }
    const contact = {
      background:`url(${I.postBox})  no-repeat`,
      backgroundPosition:"center top",
      backgroundSize:"auto 100vh",
      minHeight: '100vh',  
      backgroundColor: "#005"
    } 
    const contactWordArea = {
      backgroundColor: '#005',
      maxWidth: 600,
      padding: 48,
      borderRadius: 10,
      // marginBottom: 104,
    }
    const greatWord = {
      fontSize: 30,
      color: '#FFF',
      // fontWeight: 'bolder',
      marginBottom: 24,
      fontStyle: 'oblique',
      fontFamily: 'ＭＳ 明朝',
      lineHeight: 1.5,
    }
    const greatMan = {
      fontSize: 20,
      color: '#FFF',
      marginBottom: 48,
      fontFamily: 'serif',
    }
    return(
      <Fragment>
        <C.Row style={ topBg }>
          <C.Row center middle style = { top }>
            <C.Col middle center>
              <C.P label="就活＜終活" style={ catchcopy } responsive={{ S:S.font(50), M:S.font(75)}} />
              <C.P label="内定で人生終わりじゃない" style={ subCatchcopy } responsive={{ S:S.font(20), M:S.font(30)}} />
            </C.Col>
          </C.Row>
          <C.Row  center nowrap style = { top }>
            <C.Col center>
              <p></p>
              <C.Btn label='お問い合わせ' onClick={toContactPage} color='#ea0' style={ contactBtn } responsive={{S : contactBtn_S, M : contactBtn_M}} />
            </C.Col>
            {/* <C.Col style={ topImage2 } responsive={{S:S.none}}/> */}
          </C.Row>
        </C.Row>
        {/* ___________________________________________________________ */}

        <C.Row style={ section }>
          <C.Col span="12">
            <C.Row center middle style={ sectionTitle }>
              学生就活応援団SUCCESSについて
            </C.Row>
            <C.Row　center>
              <C.Col center>
                <C.P label="私たちは学生の就職活動を支援しているボランティア団体です。" style={ aboutUsSentence } responsive={{S: {lineHeight: null }, M:{lineHeight: 2.5} }}/>
                <C.P label="しかし、私たちのミッションは内定さえ取れればいいという安易なものではありません。" style={ aboutUsSentence } responsive={{S: {lineHeight: null }, M:{lineHeight: 2.5} }}/>
                <C.P label="内定獲得後も豊かな人生を歩んでいただくこと、それが私たちのミッションです。" style={ aboutUsSentence } responsive={{S: {lineHeight: null }, M:{lineHeight: 2.5} }}/>
                <C.P label="そして、そのためのサポートがしたいという志を持ったメンバーが集まってできました。" style={ aboutUsSentence } responsive={{S: {lineHeight: null }, M:{lineHeight: 2.5} }}/>
                {/* <C.Link label="もっと詳しく" /> */}
              </C.Col>
            </C.Row>
            <C.Row center>
              {/* <C.Col center>
                <C.Icon src={ I.coin } style={ aboutUsIcon } />
                <p>講座費用：無料</p>
              </C.Col> */}
              <C.Col center>
                <C.Icon src={ I.menber } style={ aboutUsIcon } />
                <p>メンバー構成</p>
                <p>メンバー数：30人（2019年4月）</p>
                <p>男女比率　1:1</p>
              </C.Col>
              <C.Col center>
                <C.Icon src={ I.coin } style={ aboutUsIcon } />
                <p>講座費用：無料</p>
              </C.Col>
            </C.Row>
          </C.Col>
        </C.Row>
        {/* ___________________________________________________________ */}

        <C.Row style={ section }>
          <C.Col  span="12">
            <C.Row center middle style={ sectionTitle }>
              論理的で実践的なサポート
            </C.Row>
            <C.Row center >
              <C.Col center >
                <p style={ aboutActHeading } >『偉大な成功者たちの生き方を研究』</p>
                <p style={ aboutActSentence }>
                  偉大なる成功者たちには、ある共通点がありました。
                  それは、体力づくりです。それも”ただの体力”づくりではなく、”心”の体力づくりに注力していたということです。
                  誰もがファッションやダイエット、筋トレといった、いわば外側の体力づくりに時間をかける中、偉大なる成功者たちは、
                  精神力や集中力、インスピレーションといった、内側の体力づくりを重視していたのです。
                  では、内側の、心の体力を測定するためには、どのようにすればいいのでしょうか。
                </p>
              </C.Col>
              <C.Col>
                <C.Icon src={ I.library } style={ aboutActImg } />
              </C.Col>
            </C.Row>
            <C.Row center reverse>
              <C.Col>
                <C.Icon src={ I.marketing } style={ aboutActImg } />
              </C.Col>
              <C.Col center　>
                <p style={ aboutActHeading } >『研究を基にした徹底的な自己分析サポート』</p>
                <p style={ aboutActSentence }>
                　それが、エゴグラムやエニアグラムなどを利用した自己分析なのです。
                  いわば、自己分析とは、”心”の基礎体力を測る体力テストのようなものなのです。
                  客観的に、自分の体力を測り、自分には何ができるのかできないのか、それらを見える化する手段なのです。
                  ただ、いくら知識があったとしても、その知識を使いこなすことができなければ、せっかく手に入れた知識は、宝の持ち腐れになってしまいます。
                  ですから、得た結果を今後の自分に、どのように活かしていくかが重要になります。
                  では、どのように活かしていけばいいのでしょうか。                
                </p>
              </C.Col>
            </C.Row>
            <C.Row center>
              <C.Col center>
                <p style={ aboutActHeading } >『実戦を重視』</p>
                <p style={ aboutActSentence }>
                  それが、もう一つの共通点である、行動することなのです。
                  私たちは誰しも、自分の中の常識に縛られ、なかなか行動することができません。
                  すなわち、自分の考え、価値観が、自分の行動を制限してしまっているのです。
                  偉大なる成功者たちは、それにいち早く気づき、行動を起こし、行動し続け、
                  これまでの自分を制約してきた考え、価値観を手放し、新たな考え、価値観を手に入れ、成功を修めていきました。
                  つまり、何もしなければ失敗はしませんが、成功することもないということなのです。
                  ですから、私たちは行動すること、すなわち、実践を重視しています
                </p>
              </C.Col>
              <C.Col>
                <C.Icon src={ I.success_step2 } style={ aboutActImg } /> 
              </C.Col>
            </C.Row>
          </C.Col>
        </C.Row>
        {/* ___________________________________________________________ */}

        <C.Row style={ section }>
          <C.Col  span="12">
            <C.Row center middle style={ sectionTitle }>
              こんな方には特におすすめ
            </C.Row>
            <C.Row center>
              <C.Col center>
                <C.Icon src={ I.whiteMan1 } style={ recommendImg } />
                <p  style={ recommendWords }>『本当にやりたいことが分からない』</p>
              </C.Col>
              <C.Col center>
                <C.Icon src={ I.whiteMan3 } style={ recommendImg } />
                <p style={ recommendWords }>『これから社会に出て成功したい』</p>
              </C.Col>
              <C.Col center>
                <C.Icon src={ I.whiteMan2 } style={ recommendImg } />                
                <p style={ recommendWords }>『今は楽しいけど本当にこのままでいいのだろうか』</p>
              </C.Col>
            </C.Row>
            <C.Row center>
              <C.Col center>
                <C.Icon src={ I.whiteMan4 } style={ recommendImg } />
                <p style={ recommendWords }>『自己分析が苦手』</p>
              </C.Col>              
              <C.Col center>
                <C.Icon src={ I.whiteMan5 } style={ recommendImg } />
                <p style={ recommendWords }>『プライベートも充実させたい』</p>
              </C.Col>
            </C.Row>
          </C.Col>
        </C.Row>
        {/* ___________________________________________________________ */}

        <C.Row right style={ contact }>
          <C.Col middle span="6" S="12" L="6">
            <C.Col style={ contactWordArea } responsive={{S: S.padding(8)}}>
              <C.P label="「本当のことを知りたければ、経験を積まないといけない。それは試練だが、その先には真実がある。」" style={ greatWord } responsive={{ S:S.font(14)}}/>
              <C.P label="アルベルト・アインシュタイン" style={ greatMan } responsive={{ S:S.font(10)}}/>
              <C.Btn label="お問い合わせ" onClick={toContactPage} color="#ea0"/>
            </C.Col>
          </C.Col>
        </C.Row>
        {/* ___________________________________________________________ */}


      </Fragment>
    );
  }
}