import React from 'react';

export default class Grid extends React.Component{
  render(){
    const{ children } = this.props
    const grid = {
      display:"flex",
      width : "100%",
      // border: '3px solid black',
      minHeight:"100vh",
      flexWrap :"wrap",

    }

    return(
      <div style={ grid }>
        { children }
      </div>
    );
  }
} 